@import url(https://fonts.googleapis.com/css2?family=Source+Sans+Pro:wght@600&display=swap);
.statistics {
  padding: 55px 90px;
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.stats {
  display: flex;
  flex-direction: column;
  text-align: center;
  align-items: center;
}

.text {
  font-size: 40px;
  font-weight: 600;
  color: #ef7338;
}

.w-250 {
  width: 250px;
}

.border-orange {
  height: 4px;
  background-color: #ef7338;
}

.text-color-orange {
  font-weight: 600;
  color: #ef7338 !important;
  font-size: 36px;
}

.fs-36 {
  font-size: 36px;
}

.text-plan {
  font-size: 20px;
  font-weight: 800;
}

.Silver {
  background-color: #b3b8f7;
  padding: 1rem;
}

.Gold {
  background-color: #f9d9cf;
  padding: 1rem;
}

.Diamond {
  background-color: #fbe2da;
  padding: 1rem;
}

.Silver span,
.Gold span,
.Diamond span {
  padding: 1rem;
  margin-left: 3rem;
  justify-content: space-between;
}

.Silver span img,
.Gold span img,
.Diamond span img {
  margin-right: 1rem;
}

.Silver span {
  color: #303ff7;
}

.Gold span {
  color: #f6d60b;
}

.Diamond span {
  color: #f6150b;
}

.plan {
  cursor: pointer;
}

.save {
  background-color: #4c3cce;
  color: ghostwhite;
}


.title {
  color: #ef7338;
  font-size: 20px;
  font-weight: 600;
}

.light-title {
  color: #ef7338;
  font-size: 15px;
  font-weight: 600;
}

.link {
  height: 45px;
  width: 45px;
  display: flex;
  justify-content: center;
  align-items: center;
  border-radius: 50%;
  box-shadow: 0px 1px 2px rgb(0 0 0 / 16%);
  cursor: pointer;
}

.my-1 {
  margin: 1rem 0;
}

.email {
  font-size: 16px;
}

.mt-1 {
  margin-top: 1rem;
}

.ml-2 {
  margin-left: 2rem;
}

.mr-1 {
  margin-right: 1rem;
}

.mr-5 {
  margin-right: 5rem;
}

.contact-no {
  font-size: 26px;
}

.whatsapp {
  display: flex;
  background: #e7f4f2;
  align-items: center;
  height: 40px;
  width: 160px;
  border-radius: 4px;
  cursor: pointer;
  padding: 0 11px;
  color: #128c7e;
  margin-top: 25px;
  margin-right: 1rem;
}

.chat {
  display: flex;
  background: #c7e2ea;
  align-items: center;
  height: 40px;
  width: 160px;
  border-radius: 4px;
  cursor: pointer;
  padding: 0 11px;
  color: #15c4f3;
  margin-top: 25px;
}

.social-media {
  display: flex;
  margin-top: 10px;
  width: -webkit-max-content;
  width: max-content;
}

.item-header {
  border-top: 1px solid #eeecec;
  border-bottom: 1px solid #eeecec;
  border-right: 1px solid #eeecec;
  padding: 0.8rem;
  padding-left: 1.3rem;
}

.w-60 {
  width: 60%;
}

.w-40 {
  width: 40%;
}

.mt-3 {
  margin-top: 3rem;
}

.search {
  width: 20rem;
  padding-left: 17px;
}

.search input {
  background: url('https://img.icons8.com/material-outlined/24/000000/search--v1.png')
    no-repeat scroll 7px 7px;
  padding-left: 30px;
  padding-right: 10px;
}

th {
  text-transform: uppercase;
  height: 50px;
  background: #f7f7f7;
  font-weight: 400;
  padding-top: 15px;
  padding-bottom: 15px;
  font-size: 15px;
  line-height: 15px;
  -webkit-text-decoration-color: gray;
          text-decoration-color: gray;
}

table,
th,
td {
  border: 2px solid #eeecec;
  border-collapse: collapse;
  padding: 1.5rem;
}

body {
  margin: 0;
  font-family: 'Source Sans Pro', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

